.proyect {
    position: relative;
    margin-top: 20px;
    height: 500px;
}

.phone {
    z-index: 1;
    position: absolute;
    transform: rotate(6deg);
    bottom: 60px;
    right: 120px;
    width: 160px;
}

.notebook {
    z-index: 1;
    position: absolute;
    top: 100px;
    width: 500px;
    left: 137px;
    height: 365px;
}

.bubbleProyect {
    position: absolute;
}

.bblLeft {
    left: 215px;
    top: 120px;
}

.bblRight {
    bottom: -60px;
    right: 185px;
}

@media (max-width: 600px) {

    .bblLeft {
        top: 0;
        width: 330px;
        right: -120px;
    }

    .bblRight {
        bottom: 0;
        left: -285px;
    }

    .proyect {
        position: relative;
        margin-top: 50px;
        height: 260px;
    }

    .notebook {
        position: absolute;
        top: 20px;
        left: 20px;
        width: 245px;
        height: 185px;
    }

    .phone{
        position: absolute;
        transform: none;
        top: 20px;
        right: 20px;
        width: 95px;
        height: 185px;
    }
  }