.sliderImg {
    height: 660px;
    width: 100%;
    position: relative;
}

.description {
    width: 100%;
    position: absolute;
    top: 235px;
    color: white;
}

.description h1 {
    margin: 5px;
    font-size:  xxx-large;
    letter-spacing: 2px;
}

.description h2 {
    margin: 5px;
    font-size:  xx-large;
    letter-spacing: 2px;
}

@media (max-width: 600px) {
    .sliderImg {
        display: none;
    }

    .phoneImg {
        display: block !important;
        width: 100%;
        height: 400px;
    }

    .description {
        width: 100%;
        border-radius: 4px;
        position: absolute;
        top: 115px;
        color: white;
    }

    .description h1 {
        margin: 5px;
        font-size: xx-large;
        letter-spacing: 2px;
    }

    .description h2 {
        margin: 5px;
        font-size: x-large;
        letter-spacing: 2px;
    }
}

@media (max-width: 1920px) {

    .phoneImg {
        display: none;
    }
}