.contact {
    position: relative;
    padding-bottom: 150px;
}

.bbleLeft {
    position: absolute;
    left: 0;
    top: 170px;
}

.bbleRight {
    position: absolute;
    right: 0;
    bottom: 0;
}

.formAndMap {
    margin-top: 50px;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
}

.map {
    width: 100%;
    height: 355px;
    border-radius: 4px;
    border: none;
}

.icons {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 53px;
}

.cntMap {
    margin: 20px;
    width: 100%;
}

@media (max-width: 600px) {
    .contact {
        padding-bottom: 40px;
    }

    .formAndMap {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }

    .cntMap {
        margin: 0;
        width: 100%;
    }

    .map {
        margin-top: 40px;
        width: 100%;
        height: 200px;
    }
}