.about {
    position: relative;
}

.bubble {
    position: absolute;
}

.right {
    right: 0;
    top: 15px;
}


.left {
    left: 0;
    bottom: -65px;
}

.content {
    color: #707070;
    text-align: left;
    width: 70%;
    padding: 15px;
}

.content p {
    font-size: 20px;
}

@media (max-width: 600px) {
    .about {
        overflow: hidden;
    }
    
    .content {
        width: 95%;
        background-color: white;
        z-index: 1;
        box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
        border-radius: 4px;
        padding: 30px 25px;
    }

    .skills {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    .left {
        left: 0px;
        bottom: 0;
    }
  }